.card, .card-home {
	border: 1px solid var(--card-col-b);
	box-shadow: 0px 0px 7px var(--card-col-b);
	a {text-decoration: none;}
}

figure {max-width: 100%;}
figure img {max-width: 100%; height: fit-content;}
figcaption {
	display: flex;
	justify-content: flex-start;
	flex-direction: column;
	padding: 3%;
}

section {
	display: flex;
	justify-content: center;
}

h4 {color: var(--head-fourth-col); font-weight: bold;}

.flip-card {
  background-color: transparent;
  width: 400px;
  perspective: 1000px;
	
  .inner {
    position: relative;
    width: 100%;
    height: 0%;
    transition: transform 0.8s;
    transform-style: preserve-3d;
		padding-top: 137.5%;

    .front,
    .back {
      position: absolute;
      top: 0;
			left: 0;
      height: 100%;
      -webkit-backface-visibility: hidden;
      backface-visibility: hidden;
      display: flex;
      align-items: center;
      justify-content: flex-start;
      flex-direction: column;
    }
    .front {
			width: 100%;
      background-color: var(--card-col-bg_front);
    }
    .back {
			width: 88%;
			padding: 0 6%;
      background-color: var(--card-col-bg_back);
      color: var(--card-col_back);
      transform: rotateY(180deg);
			justify-content: flex-start;
    	overflow-y: auto;
			h6 {padding-bottom: 1%; color: var(--head-sixth-col); text-shadow: 1px 1px 3px var(--head-sixth-col-sdw); font-weight: bold;}
			p {color: var(--paragraph-col); text-shadow: 1px 1px 3px var(--paragraph-col-sdw);
				strong{font-weight: bold;}
			}
			& button{
				position: absolute;
				right: 0;
				top: 0;
				background-color: transparent;
				border: none;
				color: var(--card_col_btn-close);
				background-color: var(--card_col-bg_btn-close);
				width: 25px;
				height: 25px;
				border-radius: 5px;
				border-top-right-radius: 0px;
				border: 1px solid;
				border-color: var(--card_col-b_btn-close);
			}
			.info {
				margin-top: 4%;
				display: flex;
				padding-bottom: 2%;
				flex-wrap: wrap;
				justify-content: space-between;

				.feature {
					width: 98%;
					padding: 0 2% 1%;
				  display: flex;
				  justify-content: space-between;
				}
			}
			.list-feature {
				width: 100%;
				padding-bottom: 2%;
				.info{
					margin-top: 2%;
					width: 96%;
					padding: 0 2% 1%;
					display: flex;
					justify-content: space-between;
					flex-wrap: nowrap;
				}
				.feature {
					display: flex;
					width: 100%;
					padding-left: 5%;
					
					p {
						display: inline-flex;
						width: 48%;
						flex-direction: column;
						padding: 0 1%;

						& strong {
							display: inline-flex;
							align-items: flex-start;
						}
					}
				}
			}
    }
		&.flip {
			transform: rotateY(180deg);
		}
  }
	
}

@media screen and (max-width: 848px) {
	.flip-card {
		.inner{
			.back{
				padding: 0 9% 0 3%;
				button{width: 35px; height: 35px;}
			}
		}
	}
}

@media screen and (max-width: 430px) {
  section {margin: 0 5px;}
	.flip-card {
		width: 100%;
		max-width: 400px;
	}
}

@media screen and (max-width: 410px) {
  section {margin: 0 5px;}
}